<template>
  <div class="content" style="overflow: hidden;">
    <!--搜索-->
    <div class="top_header">
      <img src="../assets/images/back_left_blank.png" @click="back">
      <div class="search">
        <img src="../assets/images/icon_search.png">
        <input type="text" placeholder="输入商家或商品名称"/>
      </div>
      <span class="cancel">取消</span>
    </div>
    <!--筛选-->
    <div class="select">
      <div class="state" @click="showState">
        <span v-text="state" class="screen_text" :title="state"></span>
        <img src="../assets/images/icon_down.png">
      </div>
      <div class="begin_time" @click="showBrand">
        <span v-text="brand" class="screen_text" :title="brand"></span>
        <img src="../assets/images/icon_down.png">
      </div>
      <div class="end_time" @click="showShelves">
        <span v-text="shelves" class="screen_text" :title="shelves"></span>
        <img src="../assets/images/icon_down.png">
      </div>
    </div>
    <!--商品列表-->
    <div class="item" v-for="(item,index) in goodsData" @click="checkDetails(item.g_id)">
      <div class="item-title">
        <span class="order_no">商品编号：<span v-text="item.g_code"></span></span>
        <span class="oreder_state" v-if="item.g_status == 1">待审核</span>
        <span class="oreder_state" v-if="item.g_status == 2">待上架</span>
        <span class="oreder_state" v-if="item.g_status == 3">打回</span>
        <span class="oreder_state" v-if="item.g_status == 4">审核通过自动上架</span>
        <span class="oreder_state" v-if="item.g_status == 5">上架</span>
        <span class="oreder_state" v-if="item.g_status == 6">下架</span>
        <span class="oreder_state" v-if="item.g_status == 7">拒绝</span>
      </div>
      <ul class="order_info">
        <li id="order_name">
          <img :src="fileHost + item.g_imgs" alt="">
          <div class="product_price">
            <p class="product_title" v-text="item.g_name"></p>
            <p class="product_spec"><i class="shop_icon"></i>商品品类：<span v-text="item.gc_name"></span></p>
            <p class="product_spec"><i class="shop_icon"></i>添加时间：<span v-text="item.g_add_time"></span></p>
          </div>
        </li>
      </ul>
    </div>
    <div class="bar_tite">没有更多啦~</div>
    <!--审核状态弹出框-->
    <van-popup v-model.trim="isShowState" position="bottom">
      <van-picker
        show-toolbar
        :columns="showStateData"
        @confirm="stateConfirm"
        @cancel="isShowState = false"
      />
    </van-popup>
    <!--品牌弹出框-->
    <van-popup v-model.trim="isShowBrand" position="bottom">
      <van-picker
        show-toolbar
        :columns="brandData"
        @confirm="brandConfirm"
        @cancel="isShowBrand = false"
      />
    </van-popup>
    <!--上下架弹出框-->
    <van-popup v-model.trim="isShowShelves" position="bottom">
      <van-picker
        show-toolbar
        :columns="ShelvesData"
        @confirm="shelvesConfirm"
        @cancel="isShowShelves = false"
      />
    </van-popup>
    <div class="product_add_box">
      <router-link to="/addGoods">
        <img class="add_product" src="../assets/images/icon_product_add.png"/>
      </router-link>
    </div>
  </div>
</template>
<script>
  import * as API from '../service/API';
  import Toast from "../widget/Toast";
  import Loading from '../widget/loading/loading'
  export default {
    data() {
      return {
        currentSelected:1 , //1 赊购，2全款
        goodsData:[],      //商品列表內容
        state:'状态',
        brand:'品牌',
        shelves:'上下架',
        isShowState : false,  //底部状态弹出框
        isShowBrand: false,   // 品牌弹出框
        isShowShelves: false, // 上下架弹出框
        showStateData:[],     //选择状态
        brandData:[],     //选择品牌
        ShelvesData:[],     //选择上下架
        fileHost:'',

      };
    },
    mounted () {
      let screenHeigt = window.screen.availHeight;
      document.getElementsByClassName('content')[0].style.minHeight=screenHeigt+'px';
      document.getElementsByClassName('content')[0].style.backgroundColor='#eee';
      this.getGoodList();
      this.getFileHost();
    },
    methods: {
      getFileHost(){
        this.fileHost = process.env.ALY_IMG_URL;
      },
      switchTab(tab){
        this.currentSelected = tab;
      },
      checkDetails(id){
        this.$router.push({path: `/goodDetails/${id}`});
      },
      getGoodList(){
        let loading = new Loading();
        loading.show();
        this.$get(API.GOODS_LIST).then((res)=>{
          if(res.code != 200){
            new Toast(res.msg).show();
            return
          }
          this.goodsData = res.goods_list;
          if(this.goodsData&&this.goodsData.length==0){
            this.$router.replace('/addProduct');
          }
          loading.close();
        }).then((error)=>{
          loading.close();
        });
      },
      back(){
        this.$router.go(-1);
      },
//      显示状态
      showState(){
        let loading = new Loading();
        loading.show();
        this.$get(API.GOODS_LIST).then((res)=>{
          if(res.code != 200){
            new Toast(res.msg).show();
            return
          }
          let stateData = [];
          stateData = res.check_status;
          for (var i in stateData){
            this.showStateData.push(stateData[i].label);
          }
          loading.close();
        }).then((error)=>{
          loading.close();
        });
        this.isShowState = true;
      },
//      状态确定
      stateConfirm(value, index){
        this.state = value;
        this.isShowState = false;
      },

//      显示品牌
      showBrand(){
        let loading = new Loading();
        loading.show();
        this.$get(API.GOODS_LIST).then((res)=>{
          if(res.code != 200){
            new Toast(res.msg).show();
            return
          }
          let brandData = [];
          brandData = res.brand_info;
          for (var i in brandData){
            this.brandData.push(brandData[i].gbName);
          }
          loading.close();
        }).then((error)=>{
          loading.close();
        });
        this.isShowBrand = true;
      },
//      品牌选择确定
      brandConfirm(value,index){
        this.brand = value;
        this.isShowBrand = false;
      },

//      显示上下架
      showShelves(){
        let loading = new Loading();
        loading.show();
        this.$get(API.GOODS_LIST).then((res)=>{
          if(res.code != 200){
            new Toast(res.msg).show();
            return
          }
          let shelvesData = [];
          shelvesData = res.status;
          for (var i in shelvesData){
            this.ShelvesData.push(shelvesData[i].label);
          }
          loading.close();
        }).then((error)=>{
          loading.close();
        });
        this.isShowShelves = true;
      },
//      上下架确定
      shelvesConfirm(value,index){
        this.shelves = value;
        this.isShowShelves = false;
      },
    }
  }
</script>
<style lang="scss" scoped>
  @import "../style/common.scss";
  /********header*********/
  .top_header{
    width: 100%;
    position: fixed;
    left: 0;
    top:0;
    overflow: hidden;
    height: 1.9rem;
    font-size: 0;
    background-color: white;
    border: none!important;
  }
  .top_header img{
    width: 1.61rem;
    height: 1.61rem;
  }
  .top_header .search{
    width: 68%;
    display: inline-block;
    line-height: 0.78rem;
    background-color: #eeeeee;
    border-radius: 0.39rem;
  }
  .top_header .cancel{
    display: inline-block;
    font-size: .48rem;
    color: #4cc3ad;
    float: right;
    margin-right: .5rem;
  }
  .top_header .search img{
    padding-left: 0.5rem;
    width: 0.48rem;
    height: 0.48rem;
  }
  .top_header .search input {
    border: none;
    outline: none;
    background-color: transparent;
    font-size: 0.4rem;
    vertical-align: middle;
    margin-left: .2rem;
  }

  /*******select*******/
  .select{
    margin-top: 1.61rem;
    height: 1.12rem;
    padding-bottom: .34rem;
    background-color: white;
    display: flex;
  }
  .select span{
    color: #999999;
    font-size: .4rem;
  }
  .select img{
    padding-top: .2rem;
    width: .25rem;
    height: .13rem;
  }
  .select .state{
    margin-left: .54rem;
  }
  .select .begin_time,.end_time{
    margin-left: .92rem;
  }
  /*******筛选span*****/
  .screen_text{
    display: inline-block;
    max-width: 3.5rem;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    vertical-align: middle;
  }
  /********item*********/
  .item{
    margin-top: 0.19rem;
    background-color: white;
  }
  .item .item-title{
    margin: 0 0.5rem;
    padding: 0.37rem 0;
    color: #999999;
    font-size: 0.36rem;
    border-bottom: #eeeeee 1px solid;
    display: flex;
    justify-content: space-between;
  }
  .item .item-title .oreder_state{
    color: #db6045;
  }
  .item .order_info{
    margin: 0.18rem 0.5rem 0.28rem 0.5rem;
    padding-bottom: 0.28rem;
    color: $font_100;
    font-size: 0.4rem;
    border-bottom: 1px solid #eeeeee;
  }
  .item ul li{
    margin-top: 0.3rem;
  }
  .item li img{
    width: 2.1rem;
    height: 2.4rem;
  }
  .product_price{
    display: inline-block;
    vertical-align: top;
    padding-left: .2rem;
  }
  .product_price .product_title{
    font-size: .48rem;
  }
  .product_price .product_spec{
    font-size: .32rem;
    color: #989898;
    padding-top: .15rem;
  }
  .shop_icon{
    display: inline-block;
    width: .4rem;
    height: .4rem;
    background: url("../assets/images/icon_shop_add_time.png") no-repeat center;
    background-size: cover;
    vertical-align: middle;
    margin-right: .1rem;
  }
  .product_money{
    font-size: .52rem;
    color: #e36348;
    margin-top: .4rem;
  }
  /*  提示  */
  .bar_tite{
    font-size: .32rem;
    text-align: center;
    color: #989898;
    margin: .2rem 0 .5rem;
  }
  .product_add_box{
    position: fixed;
    right: 0.58rem;
    bottom: 3.28rem;
    .add_product{
      width: 1.83rem;
      height: 1.79rem;
    }
  }
</style>



